.navbar {
    position: fixed;
    left: 0%;
    top: 0%;
    right: 0%;
    bottom: auto;
    z-index: 3;

    display: flex;
    height: fit-content;
    flex-direction: column;
    align-items: center;
    font-weight: 600;
    font-size: 2em;

