}

.nav-bg{
    position: fixed;
    height: 4em;
    width: 100%;
    top: 0;
    z-index: -1;
    background-color: #111;
}


.nav-main {
    display: inline-flex;
    width: fit-content;
    color: darkgray;

    font-size: 1.25em;
    text-decoration: none;
    margin-top: auto;
    margin-bottom: auto;
    white-space: nowrap;

    /* display: flex;
    font-size: 2vw;
    text-decoration: none;
    color: gray;
    margin-top: auto;
    margin-bottom: auto; */
    /* white-space: nowrap; */
}
.nav-main:hover {
    color: azure;
}
.h1.nav-main {
    display: inline-flex;
}

.nav-logo {
    width: 20%;
    height: auto;
    margin-right: 2%;
}

nav {
    flex: 1;
    /* width: fit-content; */
    /* position: relative; */
    /* float: right; */
    /* display: inline-flex; */
    /* margin-left:auto; */
    margin-inline: 5%;
    /* margin: auto 5%; */

    /* margin-top: 0; */
    /* margin-bottom: auto; */
}
ul {
    display: inline-flex;
    float: right;
    justify-content: flex-end;
}
li {
    display: inline-block;
    padding-inline: 1rem;
    text-align: center;
}


.nav-link {
    flex: 1;
    min-width: 0;
    width: fit-content;
    height: fit-content;
    color: darkgray;
    text-decoration: none;
    text-align: center;

    padding-inline: 5%;

    white-space: nowrap;
}
.nav-link:hover {
    color:aliceblue;
    text-decoration: underline;
}